import React, { useEffect, useState } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { Panel, PanelHeader, Separator, List, PanelHeaderBack, Cell, Button, Avatar, Group, Header} from '@vkontakte/vkui/';
import Icon28ShareOutline from '@vkontakte/icons/dist/28/share_outline';
import Icon28DeleteOutline from '@vkontakte/icons/dist/28/delete_outline';
import { firebase } from './Firebase.config.js';

const Rating = (props) => {

	const [photo, setStatePhoto] = useState()
	const [userName, setStateUserName] = useState()
	const [rating, setStateRating] = useState(0)
	const [top, setTop] = useState([])

	useEffect(() => {

		firebase.firestore().collection('rating').get()
			.then((snapshot) => {
				const allusers = snapshot.docs.map ((obj) => (
					{...obj.data(),docid: obj.id}
				))
				setTop(allusers)
			}).catch(err=>{
			 console.log(err)
			})



		bridge.send("VKWebAppStorageGet", { "keys": ["rating"] })
			.then(data => {
				console.log(data.keys[0].value);

				if (data.keys[0].value === '') {
					setStateRating(0 + ' ')
				}
				else {
					setStateRating(data.keys[0].value)
				}
			})

		bridge.send("VKWebAppGetUserInfo")
			.then(data => {
				setStatePhoto(data.photo_200)
				setStateUserName(data.first_name + ' ' + data.last_name)
			})

	}, [firebase])

	const resetRating = () => {
		bridge.send("VKWebAppStorageSet", { "key": "rating", "value": '0 ' })
		setStateRating('0 ')
	}


	return (

		<Panel id={props.id} separator={false}>
			<PanelHeader left={<PanelHeaderBack onClick={() => window.history.back()} />}>
				
        </PanelHeader>
			<div className='stage'>
				<ul class="circles">
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>

				</ul>
				<div style={{ position: 'relative', marginTop: '21vh' }}>
				<Separator/>
					<Group header={
						<Header> </Header>}>
						{top.map(user => ( 
							<>
								<Cell>{user.FirstName} {user.LastName} {user.score}</Cell>
							</>
								
						))}

					</Group>
				</div>
			</div>
		</Panel>
	)
};

export default Rating;





